/* Flexgrid */

	#flexgrid {
		width: 100%;
		margin: 0 0 2.5rem 0;
		display: -moz-flex;
		display: -webkit-flex;
		display: -ms-flex;
		display: flex;
		-moz-flex-wrap: wrap;
		-webkit-flex-wrap: wrap;
		-ms-flex-wrap: wrap;
		flex-wrap: wrap;
		-moz-align-items: stretch;
		-webkit-align-items: stretch;
		-ms-align-items: stretch;
		align-items: stretch;
	}

		#flexgrid > * {
			-moz-flex-shrink: 1;
			-webkit-flex-shrink: 1;
			-ms-flex-shrink: 1;
			flex-shrink: 1;
			-moz-flex-grow: 0;
			-webkit-flex-grow: 0;
			-ms-flex-grow: 0;
			flex-grow: 0;
		}

		#flexgrid > * {
			width: 33.33333%;
		}

		#flexgrid > * {
			padding: 3rem;
			width: calc(33.33333% + 2rem);
		}

		#flexgrid > :nth-child(-n + 3) {
			padding-top: 0;
		}

		#flexgrid > :nth-last-child(-n + 3) {
			padding-bottom: 0;
		}

		#flexgrid > :nth-child(3n + 1) {
			padding-left: 0;
		}

		#flexgrid > :nth-child(3n) {
			padding-right: 0;
		}

		#flexgrid > :nth-child(3n + 1),
		#flexgrid > :nth-child(3n) {
			width: calc(33.33333% + -1rem);
		}

		#flexgrid > div {
			border-style: solid;
			border-color: rgba(255, 255, 255, 0.25);
			border-left-width: 1px;
			border-top-width: 1px;
			text-align: center;
		}

			#flexgrid > div > :last-child {
				margin-bottom: 0;
			}

			#flexgrid > div:nth-child(3n - 2) {
				border-left-width: 0;
			}

			#flexgrid > div:nth-child(-n + 3) {
				border-top-width: 0;
			}

			#flexgrid > div .icon {
				display: -moz-flex;
				display: -webkit-flex;
				display: -ms-flex;
				display: flex;
				-moz-align-items: center;
				-webkit-align-items: center;
				-ms-align-items: center;
				align-items: center;
				-moz-justify-content: center;
				-webkit-justify-content: center;
				-ms-justify-content: center;
				justify-content: center;
				font-size: 3.5rem;
				background: #fff;
