				width: 8rem;
				height: 8rem;
				border-radius: 100%;
				margin: 0 auto 2rem auto;
			}

		@media screen and (max-width: 980px) {

			#flexgrid > * {
				width: 100%;
			}

			#flexgrid > * {
				padding: 2rem;
